<template>
  <div class="item__desc">
    <ul class="tabs">
      <li class="tabs__item">
        <a class="tabs__link" :class="{ 'tabs__link--current': aboutProduct }"
        @click.prevent="chengeAboutContent(aboutProduct)"
        @keyup.enter="chengeAboutContent(aboutProduct)">
          Информация о товаре
        </a>
      </li>
      <li class="tabs__item">
        <a class="tabs__link" :class="{ 'tabs__link--current': deliveryInfo }"
        @click.prevent="chengeDeliveryContent(deliveryInfo)"
        @keyup.enter="chengeDeliveryContent(deliveryInfo)">
          Доставка и возврат
        </a>
      </li>
    </ul>

    <ItemContentAboutProduct v-if="aboutProduct" />

    <ItemContentDelivery v-if="deliveryInfo" />

  </div>
</template>

<script>
import ItemContentAboutProduct from '../Item/ItemContentAboutProduct.vue';
import ItemContentDelivery from '../Item/ItemContentDelivery.vue';

export default {
  data() {
    return {
      aboutProduct: true,
      deliveryInfo: false,
    };
  },
  components: { ItemContentAboutProduct, ItemContentDelivery },

  methods: {
    chengeAboutContent() {
      this.deliveryInfo = false;
      this.aboutProduct = true;
    },
    chengeDeliveryContent() {
      this.aboutProduct = false;
      this.deliveryInfo = true;
    },
  },
};
</script>
