<template>
  <router-link class="header__cart" :to="{ name: 'cart' }" aria-label="Корзина с товарами">
    <svg width="19" height="24">
      <use xlink:href="#icon-cart"></use>
    </svg>
    <span v-if="indicatorLoading" class="header__count" aria-label="Количество товаров">
      <img src="../../../public/img/preloader/preloader.gif" alt="Preloader.gif"
      style="position: absolute; top: 0; left: 0;">
    </span>
    <span v-else class="header__count" aria-label="Количество товаров">
      {{ $store.state.cartProducts.length }}</span>
  </router-link>
</template>

<script>
export default {
  data() {
    return {
      indicatorLoading: true,
    };
  },
  methods: {
    loadIndicator() {
      setTimeout(() => {
        this.indicatorLoading = false;
      }, 2000);
    },
  },
  created() {
    this.loadIndicator();
  },
};
</script>
