<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <ul class="colors colors--black">
    <li class="colors__item" v-for="item in colors" :key="item.color.id">
      <label class="colors__label">
        <input class="colors__radio sr-only" type="radio"
        :value="item.color.id" v-model.number="selectId">
        <span class="colors__value" :style="{ 'background-color': item.color.code }">
        </span>
      </label>
    </li>
  </ul>
</template>

<script>
export default {
  data() {
    return {
      selectId: this.selectColorId,
    };
  },
  props: ['selectColorId', 'colors'],

  watch: {
    selectId: {
      handler(newColorId) {
        this.$emit('update:selectColorId', newColorId);
      },
    },
  },
};
</script>
