<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable max-len -->
<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.image" width="120" height="120" :alt="item.imageName">
    </div>
    <h3 class="product__title">
      {{ item.title }}
    </h3>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="{ 'background-color': item.color.code }"></i>
        {{ item.color.title }}
      </span>
    </p>
    <p class="product__info" style="margin-top: 70px;">
      Размер:
      <span>
        {{ item.size.title }}
      </span>
    </p>
    <span class="product__code">
      Артикул: {{ item.productId }}
    </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click.prevent="minusProduct" @keyup.enter="minusProduct">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count">

      <button type="button" aria-label="Добавить один товар" @click.prevent="plusProduct">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ item.price | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" @click.prevent="deleteProduct(item.basketItemId)"
      aria-label="Удалить товар из корзины">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  props: ['item'],
  filters: {
    numberFormat,
  },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { basketItemId: this.item.basketItemId, amount: value });
      },
    },
  },
  methods: {
    ...mapActions(['deleteCartProducts']),
    deleteProduct(basketItemId) {
      this.deleteCartProducts(basketItemId);
    },
    minusProduct() {
      let productAmount;
      if (this.amount !== 0) {
        productAmount = this.amount - 1;
      } else {
        productAmount = 0;
        return productAmount;
      }
      this.amount = productAmount;
      return this.amount;
    },
    plusProduct() {
      this.amount += 1;
      return this.amount;
    },
  },
};
</script>
