<!-- eslint-disable max-len -->
<template>
  <div class="item__pics pics">
    <div class="pics__wrapper">
      <img width="570" height="570" :src="selectImage" :alt="product.title">
    </div>
    <ul class="pics__list">
      <li class="pics__item" v-for="item in imageArr" :key="item.name">
        <a href="#" class="pics__link" :class="{ 'pics__link--current': item.url === selectImage }"
        @click.prevent="chengeImage(item.url)">
          <img width="98" height="98" :src="item.url"
            :alt="item.originalName">
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectImage: this.image,
    };
  },
  props: ['image', 'product', 'imageArr'],

  methods: {
    chengeImage(selectImage) {
      this.selectImage = selectImage;
    },
  },
  watch: {
    selectImage: {
      handler(newSelectImage) {
        this.$emit('update:image', newSelectImage);
      },
    },
    image: {
      immediate: true,
      handler(newImage) {
        this.selectImage = newImage;
      },
    },
  },
};
</script>
