import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],

    userAccessKey: null,
    cartProductsData: [],

    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { basketItemId, amount }) {
      const item = state.cartProducts.find((itemP) => itemP.basketItemId === basketItemId);

      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProducts(state, basketItemId) {
      state.cartProducts = state.cartProducts.filter((item) => item.basketItemId !== basketItemId);
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        basketItemId: item.id,
        color: item.color.color,
        image: item.color.gallery.map((itemI) => itemI.file.url),
        imageName: item.color.gallery.map((itemI) => itemI.file.name),
        price: item.price,
        title: item.product.title,
        size: item.size,
        amount: item.quantity,
      }));
    },
  },
  getters: {
    orderInfoDetailProducts(state) {
      return state.orderInfo ? state.orderInfo : {};
    },
    orderInfoDetailProductsBasket(state) {
      return state.orderInfo ? state.orderInfo.basket.items : [];
    },
    cartDetailProducts(state) {
      return state.cartProducts;
    },
    cartTotalPrice(state, getters) {
      // eslint-disable-next-line max-len
      return getters.cartDetailProducts.reduce((acc, item) => (item.price * item.amount) + acc, 0);
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios
        .get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(context) {
      return axios
        .get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }

          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    addProductToCart(context, productParams) {
      return (new Promise((resolve) => { setTimeout(resolve, 2000); }))
        .then(() => {
          axios
            .post(`${API_BASE_URL}/api/baskets/products`, {
              productId: productParams.productId,
              colorId: productParams.colorId,
              sizeId: productParams.sizeId,
              quantity: productParams.amount,
            }, {
              params: {
                userAccessKey: context.state.userAccessKey,
              },
            })
            .then((response) => {
              context.commit('updateCartProductsData', response.data.items);
              context.commit('syncCartProducts');
            });
        });
    },
    updateCartProductAmount(context, { basketItemId, amount }) {
      context.commit('updateCartProductAmount', { basketItemId, amount });

      if (amount < 1) {
        return;
      }

      axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          basketItemId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteCartProducts(context, basketItemId) {
      context.commit('deleteCartProducts', basketItemId);
      axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
          data: {
            basketItemId,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
  },
});
