<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Оформление заказа
        </h1>
      </div>
    </div>

    <section v-if="loadPostProduct"
      style="position: absolute; top: 0; left: 0; z-index: 100; display: flex;
      justify-content: center; align-items: center; width: 100%; height: 70%; background-color: rgba(255, 255, 255, 1);">
      <img src="../../public/img/preloader/preloader.gif" alt="Preloader.gif" style="width: 10%; height: 10%;">
    </section>
    <section v-else class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" title="ФИО" :error="formError.name"
              placeholder="Введите ваше полное имя" />

            <BaseFormText v-model="formData.address" title="Адрес доставки" :error="formError.address"
              placeholder="Введите ваш адрес" />

            <BaseFormText v-model="formData.phone" title="Телефон" type="tel" :error="formError.phone"
              placeholder="Введите ваш телефон" />

            <BaseFormText v-model="formData.email" title="Email" :error="formError.email" placeholder="Введи ваш Email" />

            <BaseFromTextarea v-model="formData.comment" title="Комментарий к заказу" :error="formError.comment"
              placeholder="Ваши пожелания" />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="delivery in methodsDelivery" :key="delivery.id">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" :value="delivery.id"
                    v-model.number="selectDelivery">
                  <span class="options__value">
                    {{ delivery.title }} <b>{{ delivery.price }}</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item" v-for="pay in methodsPay" :key="pay.id">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" :value="pay.id"
                    v-model.number="selectPay">
                  <span class="options__value">
                    {{ pay.title }}
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <ProductCheckList :products="products" :total-price="totalPrice" :title="selectDeliveryTitle" :delivery-price="deliveryPrice" />

        <div class="cart__error form__error-block" v-if="formErrorMessage !== ''">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import BaseFormText from '@/components/Base/BaseFormText.vue';
import BaseFromTextarea from '@/components/Base/BaseFormTextarea.vue';
import ProductCheckList from '@/components/Product/ProductCheckList.vue';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',

      methodsDelivery: [],
      methodsPay: [],

      deliveryPrice: null,
      selectDelivery: null,
      selectDeliveryTitle: '',
      selectPay: null,

      loadPostProduct: false,
    };
  },
  components: { ProductCheckList, BaseFormText, BaseFromTextarea },
  filters: {
    numberFormat,
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
  methods: {
    preloaderOrder() {
      setTimeout(() => {
        this.loadPostProduct = false;
      }, 2000);
      this.formData = {};
    },
    order() {
      this.loadPostProduct = true;
      this.formError = {};
      this.formErrorMessage = '';
      axios
        .post(`${API_BASE_URL}/api/orders`, {
          ...this.formData,
          deliveryTypeId: this.selectDelivery,
          paymentTypeId: this.selectPay,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        })
        .then((response) => {
          this.$store.commit('resetCart');
          this.preloaderOrder();
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          this.loadPostProduct = false;
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        });
    },
    loadMethodsDelivery() {
      axios
        .get(`${API_BASE_URL}/api/deliveries`)
        .then((response) => {
          this.methodsDelivery = response.data;
          const deliveryId = this.methodsDelivery[0].id;
          const deliveryTitle = this.methodsDelivery[0].title;
          const deliveryPrice = this.methodsDelivery[0].price;
          this.selectDelivery = deliveryId;
          this.selectDeliveryTitle = deliveryTitle;
          this.deliveryPrice = Number(deliveryPrice);
          this.loadMethodsPay(deliveryId);
        });
    },
    loadMethodsPay(deliveryTypeId) {
      axios
        .get(`${API_BASE_URL}/api/payments`, {
          params: {
            deliveryTypeId,
          },
        })
        .then((response) => {
          this.methodsPay = response.data;
          this.selectPay = this.methodsPay[0].id;
        });
    },
  },
  created() {
    this.loadMethodsDelivery();
  },
  watch: {
    selectDelivery: {
      handler(newDeliveryId) {
        this.loadMethodsPay(newDeliveryId);
        const deliveryTitle = this.methodsDelivery.find((itemD) => itemD.id === newDeliveryId);
        const deliveryPrice = Number(deliveryTitle.price);
        if (deliveryTitle && deliveryTitle.title) {
          this.selectDeliveryTitle = deliveryTitle.title;
          this.deliveryPrice = deliveryPrice;
        } else {
          this.selectDeliveryTitle = '';
          this.deliveryPrice = 0;
        }
      },
    },
  },
};
</script>
