<!-- eslint-disable max-len -->
<template>
  <main class="content container" style="position: relative;">
    <div v-if="productsLoading"
      style="position: absolute; top: 0; left: 0; z-index: 100; display: flex;
      justify-content: center; align-items: center; width: 100%; height: 70%; background-color: rgba(255, 255, 255, 1);">
      <img src="../../public/img/preloader/preloader.gif" alt="Preloader.gif" style="width: 10%; height: 10%;;">
    </div>
    <div v-if="productsLoadingFailed"
      style=" display: flex; align-items: center; flex-direction: column; font-weight: 600; font-size: 26px;text-align: center; color: red;">
      Произошла ошибка при загрузке товаров
      <button @click.prevent="loadProducts"
        style="background-color: transparent; border-radius: 10px; font-size: 18px; padding: 10px; margin-top: 20px">Попробовать
        еще раз</button>
    </div>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item" v-if="!productsLoading">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item" v-if="!productsLoading">

      <ProductItemImage :image.sync="selectImage" :product="product" :image-arr="imageArr" />

      <ProductItemInfo :product="product" :color-id.sync="selectColorId" :size-id.sync="selectSizeId"
        :amount.sync="productAmount" />

      <ProductItemDesk />

    </section>
  </main>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import ProductItemImage from '@/components/Product/ProductItemImage.vue';
import ProductItemInfo from '@/components/Product/ProductItemInfo.vue';
import ProductItemDesk from '@/components/Product/ProductItemDesk.vue';

export default {
  data() {
    return {
      productAmount: 1,

      productData: null,
      selectImage: null,
      selectColorId: null,
      selectSizeId: null,

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  components: { ProductItemImage, ProductItemInfo, ProductItemDesk },

  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category.id;
    },
    imageArr() {
      return this.productData.colors.map((itemC) => itemC.gallery[0].file);
    },
  },

  methods: {
    getProductItems() {
      const itemColors = this.product.colors.map((item) => item);
      const itemSize = this.product.sizes.map((item) => item);
      if (itemColors) {
        this.selectImage = itemColors[0].gallery[0].file.url;
        this.selectColorId = itemColors[0].color.id;
        this.selectSizeId = itemSize[0].id;
      } else {
        this.selectImage = null;
        this.selectColorId = null;
        this.selectSizeId = null;
      }
    },
    chengeColor(imageUrl) {
      const newColor = this.product.colors.find((itemC) => itemC.gallery[0].file.url === imageUrl);
      if (newColor && newColor.color.id) {
        this.selectColorId = newColor.color.id;
      } else {
        this.selectColorId = null;
      }
    },
    chengeImage(colorId) {
      const newImage = this.product.colors.find((itemC) => itemC.color.id === colorId);
      if (newImage && newImage.gallery && newImage.gallery.length > 0) {
        this.selectImage = newImage.gallery[0].file.url;
      } else {
        this.selectImage = null;
      }
    },
    loadProduct() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadingProductTimer);
      this.loadingProductTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
          .then((response) => {
            this.productData = response.data;
            this.getProductItems();
          })
          .catch(() => { this.productsLoadingFailed = true; })
          .then(() => { this.productsLoading = false; });
      }, 0);
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
    selectImage: {
      handler(newSelectImage) {
        this.chengeColor(newSelectImage);
      },
    },
    selectColorId: {
      handler(newColorId) {
        this.chengeImage(newColorId);
      },
    },
  },
};
</script>
