<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <li class="catalog__item" style="position: relative;">
    <router-link class="catalog__pic" :to="{ name: 'product', params: { id: product.id } }">
      <img v-if="!image" src="../../../public/img/not-image/not-image.png" alt="Not Image">
      <img v-else :src="image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <router-link :to="{ name: 'product', params: { id: product.id } }">
        {{ product.title }}
      </router-link>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat }} ₽
    </span>

    <ProductsItemColor :selectColorId.sync="selectColorId" :colors="product.colors" />

    <button class="item__button button button--primery button-privew" style="position: relative;"
    type="button" @click.prevent="modalWindow = true">
      В корзину
    </button>
    <ItemModalBox v-if="modalWindow" :product="product" :defaultColor="selectColorId" :defaultImage="image"
    :modal.sync="modalWindow" />
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import ProductsItemColor from './ProductsItemColor.vue';
import ItemModalBox from '../Item/ItemModalBox.vue';

export default {
  data() {
    return {
      image: null,
      selectColorId: null,

      modalWindow: false,
    };
  },
  props: ['product'],

  filters: {
    numberFormat,
  },

  components: { ProductsItemColor, ItemModalBox },

  methods: {
    productItemId() {
      const colorId = this.product.colors.map((itemC) => itemC);
      if (colorId) {
        this.selectColorId = colorId[0].color.id;
        this.image = colorId[0].gallery[0].file.url;
      } else {
        this.selectColorId = null;
        this.image = null;
      }
    },
    checkColorId(selectId) {
      const colorItem = this.product.colors.find((item) => item.color.id === selectId);
      if (colorItem && colorItem.gallery && colorItem.gallery.length > 0) {
        this.image = colorItem.gallery[0].file.url;
      } else {
        this.image = null;
      }
    },
  },
  watch: {
    selectColorId: {
      immediate: true,
      handler(newColorId) {
        this.checkColorId(newColorId);
      },
    },
  },
  created() {
    this.productItemId();
  },
};
</script>
