<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="item__info">
    <span class="item__code">Артикул: {{ product.id }}</span>
    <h2 class="item__title">
      {{ product.title }}
    </h2>
    <div class="item__form">
      <form class="form" action="#" method="POST" @submit.prevent="addToCart">
        <div class="item__row item__row--center">
          <div class="form__counter">
            <button type="button" aria-label="Убрать один товар" @click.prevent="minusProduct()">
              <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-minus"></use>
              </svg>
            </button>

            <input type="text" v-model.number="productAmount">

            <button type="button" aria-label="Добавить один товар" @click.prevent="plusProduct()">
              <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-plus"></use>
              </svg>
            </button>
          </div>

          <b class="item__price">
            {{ product.price | numberFormat }} ₽
          </b>
        </div>

        <div class="item__row">
          <fieldset class="form__block">
            <legend class="form__legend">Цвет</legend>
            <ul class="colors colors--black">
              <li class="colors__item" v-for="item in product.colors" :key="item.color.id">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio"
                  :value="item.color.id" v-model.number="selectColor">
                  <span class="colors__value" :style="{ 'background-color': item.color.code }">
                  </span>
                </label>
              </li>
            </ul>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Размер</legend>
            <label class="form__label form__label--small form__label--select">
              <select class="form__select" type="text" name="category" v-model="selectSize">
                <option :value="size.id" v-for="size in product.sizes"
                :key="size.id">{{ size.title }}</option>
              </select>
            </label>
          </fieldset>
        </div>

        <button class="item__button button button--primery" type="submit"
        :disabled="productAddSending">
          В корзину
        </button>

        <div v-show="productAdded">Товар добавлен в корзину</div>
        <div v-show="productAddSending">Добавляем товар в корзину</div>
      </form>
    </div>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      productAmount: this.amount,
      selectColor: this.colorId,
      selectSize: this.sizeId,

      productAdded: false,
      productAddSending: false,
    };
  },
  props: ['product', 'colorId', 'sizeId', 'amount'],
  filters: {
    numberFormat,
  },
  methods: {
    ...mapActions(['addProductToCart']),

    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      const productParams = {
        productId: this.product.id,
        colorId: this.selectColor,
        sizeId: this.selectSize,
        amount: this.productAmount,
      };
      this.addProductToCart(productParams)
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    minusProduct() {
      let productAmount;
      if (this.productAmount !== 0) {
        productAmount = this.productAmount - 1;
      } else {
        productAmount = 0;
        return productAmount;
      }
      this.productAmount = productAmount;
      return this.productAmount;
    },
    plusProduct() {
      this.productAmount += 1;
      return this.productAmount;
    },
    chengeColorId(selectColor) {
      this.$emit('update:colorId', selectColor);
    },
    chengeSizeId(sizeId) {
      this.$emit('update:sizeId', sizeId);
    },
    chengeAmount(amount) {
      this.$emit('update:amount', amount);
    },
  },
  watch: {
    colorId: {
      immediate: true,
      handler(newColorId) {
        this.selectColor = newColorId;
      },
    },
    sizeId: {
      immediate: true,
      handler(newSizeId) {
        this.selectSize = newSizeId;
      },
    },
    productAmount: {
      handler(newAmount) {
        this.chengeAmount(newAmount);
      },
    },
    selectColor: {
      handler(newColorId) {
        this.chengeColorId(newColorId);
      },
    },
    selectSize: {
      handler(newSizeId) {
        this.chengeSizeId(newSizeId);
      },
    },
  },
};
</script>
