<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable max-len -->
<template>
  <main v-if="productLoading" style="position: absolute; top: 0; left: 0; z-index: 100; display: flex;
          justify-content: center; align-items: center; width: 100%; height: 70%; background-color: rgba(255, 255, 255, 1);">
          <img src="../../public/img/preloader/preloader.gif" alt="Preloader.gif" style="width: 10%; height: 10%;">
  </main>
  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info">
          {{ $store.state.cartProducts.length }} товара
        </span>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.id" :item="item"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" v-if="buttonSubmit" :to="{ name: 'order' }"
          class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import CartItem from '@/components/Cart/CartItem.vue';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      productLoading: true,
      buttonSubmit: false,
    };
  },
  components: { CartItem },
  filters: {
    numberFormat,
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
  methods: {
    loadProducts() {
      setTimeout(() => {
        this.productLoading = false;
      }, 2000);
    },
    checkProduct() {
      if (this.products.length !== 0) {
        this.buttonSubmit = true;
      } else {
        this.buttonSubmit = false;
      }
    },
  },
  watch: {
    products() {
      this.checkProduct();
    },
  },
  created() {
    this.loadProducts();
    this.checkProduct();
  },
};
</script>
