<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <main class="content container">
    <div class="content__top">

      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          {{ countProducts }} товара
        </span>
      </div>
    </div>

    <div class="content__catalog">

      <ProductsFilter :category-id.sync="filterCategoryId" :price-from.sync="filterPriceForm"
        :price-to.sync="filterPriceTo" :materials.sync="filterMaterials" :seasons.sync="filterSeasons" :colors.sync="filterColors" />

      <section class="catalog" style="position: relative;">

        <div v-if="productsLoading"
          style="position: absolute; top: 0; left: 0; z-index: 100; display: flex;
          justify-content: center; align-items: center; width: 100%; height: 70%; background-color: rgba(255, 255, 255, 1);">
          <img src="../../public/img/preloader/preloader.gif" alt="Preloader.gif" style="width: 10%; height: 10%;">
        </div>
        <div v-if="productsLoadingFailed"
          style=" display: flex; align-items: center; flex-direction: column; font-weight: 600; font-size: 26px;text-align: center; color: red;">
          Произошла ошибка при загрузке товаров
          <button @click.prevent="loadProducts"
            style="background-color: transparent; border-radius: 10px; font-size: 18px; padding: 10px; margin-top: 20px">Попробовать
            еще раз</button>
        </div>

        <div v-if="productsData === null">
          Товар не найден
        </div>

        <ProductList v-if="productsData !== null" :products='products' />

        <BasePagination v-model="page" :count="countProducts" :per-page="productPerPage" />

        <div>
          <button class="button button-count" @click.prevent="chengeProductPerPage(12)">
            <span>12</span>
          </button>
          <button class="button button-count" @click.prevent="chengeProductPerPage(24)">
            <span>24</span>
          </button>
          <button class="button button-count" @click.prevent="chengeProductPerPage(32)">
            <span>32</span>
          </button>
        </div>

      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/Product/ProductList.vue';
import BasePagination from '@/components/Base/BasePagination.vue';
import ProductsFilter from '@/components/Product/ProductsFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { ProductList, BasePagination, ProductsFilter },

  data() {
    return {
      filterPriceForm: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterMaterials: [],
      filterSeasons: [],
      filterColors: [],

      page: 1,
      productPerPage: 9,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },

  computed: {
    products() {
      return this.productsData ? this.productsData.items : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },

  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              categoryId: this.filterCategoryId,
              materialIds: this.filterMaterials,
              seasonIds: this.filterSeasons,
              colorIds: this.filterColors,
              page: this.page,
              limit: this.productPerPage,
              minPrice: this.filterPriceForm,
              maxPrice: this.filterPriceTo,
            },
          })
          .then((response) => { this.productsData = response.data; })
          .catch(() => { this.productsLoadingFailed = true; })
          .then(() => { this.productsLoading = false; });
      }, 0);
    },
    chengeProductPerPage(countPerPage) {
      this.productPerPage = countPerPage;
    },
  },
  watch: {
    productPerPage() {
      this.loadProducts();
    },
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceForm() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterMaterials() {
      this.loadProducts();
    },
    filterSeasons() {
      this.loadProducts();
    },
    filterColors() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
