<!-- eslint-disable vuejs-accessibility/click-events-have-key-events -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable max-len -->
<template>
  <div>
    <div class="modal__block" @click.prevent="modalWindow = false"></div>
    <div class="modal" @click.stop>
      <router-link class="cotalog__pic-modal" :to="{ name: 'product', params: { id: product.id } }">
        <img v-if="!image" src="../../../public/img/not-image/not-image.png" alt="Not Image">
        <img v-else :src="image" :alt="product.title">
      </router-link>

      <h3 class="catalog__title">
        <router-link :to="{ name: 'product', params: { id: product.id } }">
          {{ product.title }}
        </router-link>
      </h3>

      <span class="catalog__price">
        {{ product.price | numberFormat }} ₽
      </span>

      <ProductsItemColor :selectColorId.sync="selectColorId" :colors="product.colors" />

      <div>
        <label class="form__label form__label--modal form__label--small form__label--select">
          <select class="form__select" type="text" name="category" v-model="selectSize">
            <option :value="size.id" v-for="size in sizes" :key="size.id">{{ size.title }}</option>
          </select>
        </label>
      </div>

      <button v-if="!productAdded" class="item__button button button--primery button-privew" style="position: relative;"
        type="button" @click.prevent="addToCart" :disabled="productAddSending">
        В корзину
      </button>
      <button v-if="productAdded" class="item__button button button--primery button-privew" style="position: relative;"
        type="button" @click.prevent="addToCart" :disabled="productAddSending">
        Добавить еще
      </button>
      <button v-if="productAdded" class="item__button button button--primery button-privew" style="position: relative;"
        type="button" @click.prevent="modalWindow = false" :disabled="productAddSending">
        Закрыть
      </button>
      <div v-show="productAdded" style="font-size: 14px;">Товар добавлен в корзину</div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import ProductsItemColor from '../Product/ProductsItemColor.vue';

export default {
  data() {
    return {
      image: this.defaultImage,
      selectColorId: this.defaultColor,
      sizeData: [],
      selectSize: null,

      modalWindow: this.modal,

      productAddSending: false,
      productAdded: false,
    };
  },
  props: ['product', 'defaultImage', 'defaultColor', 'modal'],

  filters: {
    numberFormat,
  },

  components: { ProductsItemColor },

  computed: {
    sizes() {
      return this.sizeData ? this.sizeData : [];
    },
  },

  methods: {
    ...mapActions(['addProductToCart']),
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      const productParams = {
        productId: this.product.id,
        colorId: this.selectColorId,
        sizeId: this.selectSize,
        amount: 1,
      };
      this.addProductToCart(productParams)
        .then(() => {
          this.productAddSending = false;
          this.productAdded = true;
        });
    },
    loadProductSize() {
      setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products/${this.product.id}`)
          .then((response) => {
            this.sizeData = response.data.sizes;
            this.selectSize = response.data.sizes[0].id;
          });
      }, 0);
    },
    checkColorId(selectId) {
      const colorItem = this.product.colors.find((item) => item.color.id === selectId);
      if (colorItem && colorItem.gallery && colorItem.gallery.length > 0) {
        this.image = colorItem.gallery[0].file.url;
      } else {
        this.image = null;
      }
    },
  },
  watch: {
    selectColorId: {
      immediate: true,
      handler(newColorId) {
        this.checkColorId(newColorId);
      },
    },
    modalWindow: {
      handler(newModal) {
        this.$emit('update:modal', newModal);
      },
    },
  },
  created() {
    this.loadProductSize();
  },
};
</script>
